<template>
  <div class="mainBox">
    <div class="fieldRow">
      <div class="title">关联小区</div>
      <div class="inputField" @click="selectDic()">
        {{ eqData.cantonName }}<van-icon name="arrow" class="rightIcon" />
      </div>
    </div>
    <div class="fieldRow">
      <div class="title">设备名称</div>
      <input type="text" v-model="eqData.name" class="inputField" />
    </div>
    <div class="fieldRow" style="align-items: start">
      <div class="title">设备地址</div>
      <textarea
        class="textareaBox"
        v-model="eqData.address"
        cols="28"
        rows="5"
      ></textarea>
    </div>
    <div class="confirmBtn" @click="submit()">修改并提交</div>
  </div>
  <van-popup
    v-model:show="show"
    position="bottom"
    closeable
    round
    safe-area-inset-bottom
    close-icon-position="top-left"
    class="popupBox"
  >
    <div class="popTitle">关联小区</div>
    <div class="popHead">
      <div class="searchRow">
        <img
          src="../assets/searchIcon.png"
          class="rowImg"
          @click="getCantonByNameFuc()"
        />
        <input
          type="text"
          v-model="searchParams"
          class="inputField"
          @input="getCantonByNameFuc()"
          placeholder="请输入小区名称"
        />
      </div>
      <span class="cancel" @click="cancel()">取消</span>
    </div>
    <div class="popContent">
      <van-radio-group v-model="checked" shape="dot">
        <div v-for="item in dataList" :key="item.id" class="radioBorder">
          <van-radio :name="item.id" checked-color="#00E07A">
            <div class="radioTitle">{{ item.name }}</div>
          </van-radio>
          <!-- <div class="radioCotnent">{{ item.fullName }}</div> -->
        </div>
      </van-radio-group>
    </div>
    <div class="popConfirm" @click="popConfirm()">确 认</div>
  </van-popup>
</template>
<script>
import { getCantonByName, getTrashCanInfo, updateTrashCanInfo } from "../api/index.js";

export default {
  name: "HomeView",
  components: {},
  data() {
    return {
      searchParams: "",
      show: false,
      checked: 1,
      no: "20240201143335c813d22a4b81415a",
      eqData: {
        address: "",
        cantonName: "",
        name: "",
      },
      dataList: [],
      routeQuery:{}
    };
  },
  mounted() {
    console.log(this.$route.query);
    this.routeQuery = this.$route.query
    this.getTrashCanInfoFuc();
  },
  methods: {
    selectDic() {
      this.show = true;
      this.getCantonByNameFuc();
    },
    cancel() {
      this.searchParams = "";
    },
    popConfirm() {
      this.show = false;
      this.eqData["cantonId"] = this.checked;
      const file = this.dataList.filter((item) => {
        return item.id == this.checked;
      });
      this.eqData["cantonName"] = file[0].name;
    },
    //获取小区
    async getCantonByNameFuc() {
      const params = { pageNum: 1, pageSize: 999, name: this.searchParams };
      const res = await getCantonByName(params);
      if (res.code == 200) {
        this.dataList = res.data;
        console.log(this.dataList, res.data);
      }
    },
    //获取配置信息
    async getTrashCanInfoFuc() {
   
      const params = { id : this.routeQuery.id };
      console.log(this.routeQuery);
      const res = await getTrashCanInfo(params);
      if (res.code == 200) {
        this.eqData = res.data;
      }
   
    },
    //提交修改
    async submit() {
      const data = this.eqData;
      const res = await updateTrashCanInfo(data);
      if (res.code == 200) {
        this.$router.push("/success");
      }
    },
  },
};
</script>
