import {
  createRouter,
  createWebHistory
} from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [{
  path: '/',
  name: 'home',
  component: HomeView,
  meta: {
    title: '修改设备信息'
  }
}, {
  path: '/success',
  name: 'success',
  component: () => import('../views/success.vue')
}]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router