import request from '@/utils/request';

export function getCantonByName(params) {
    return request({
        url: `/garbage/trashCanScree/getCantonByName`,
        method: 'get',
        params
    })
}
export function getTrashCanInfo(params) {
    return request({
        url: `/garbage/trashCanScree/getTrashCanInfoById`,
        method: 'get',
        params
    })
}
export function updateTrashCanInfo(data) {
    return request({
        url: `/garbage/trashCanScree/updateTrashCanInfo`,
        method: 'post',
        data
    })
}